import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { UserService } from './../../user.service';



@Component({
  selector: 'app-search-car',
  templateUrl: './search-car.component.html',
  styleUrls: ['./search-car.component.css'],

})


export class SearchCarComponent implements OnInit {
  userLog: any;
  carList: any;
  typeCarList: any;
  today = Date();
  pickup_Date: Date;

  constructor(private http: HttpClient, private userService: UserService, private router: Router) {
    this.http.get("http:// www.serverapi.somee.com/api/typecar/").subscribe(t => this.typeCarList = t);
    this.http.get("http:// www.serverapi.somee.com/api/car/").subscribe(t => this.carList = t);
  }
  //****function to select car by user anf checking if user registered***
  selectCar(car: any, carType: any) {
    if (this.userService.confirmLogin() == true) {
      this.userService.addCar(car, carType);
      this.userService.pickupDate = this.pickup_Date;
      this.router.navigate(['/CalcDayPrice']);
    }
    else {
      this.router.navigate(['/Login']);
    }
  }

  ngOnInit() {
    this.userLog = this.userService.loginUser;
  }

}
