import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomePageComponent } from './ClientPage/home-page/home-page.component';
import { RegisterPageComponent } from './ClientPage/register-page/register-page.component';
import { SearchCarComponent } from './ClientPage/search-car/search-car.component';
import { CalcDayPriceComponent } from './ClientPage/calc-day-price/calc-day-price.component';
import { ListCarsComponent } from './AdminPage/list-cars/list-cars.component';
import { ListOrdersComponent } from './AdminPage/list-orders/list-orders.component';
import { ListUsersComponent } from './AdminPage/list-users/list-users.component';
import { LoginPageComponent } from './login-page/login-page.component';
import { EmployeeComponent } from './employee/employee.component'
import {UserOrderComponent} from './UserPage/user-order/user-order.component';
import {UserAllOrdersComponent} from './UserPage/user-all-orders/user-all-orders.component';
import { from } from 'rxjs';

const routes: Routes = [
  { path: '', redirectTo: "HomePage", pathMatch: "full" },
  { path: "HomePage", component: HomePageComponent },
  { path: "RegisterPage", component: RegisterPageComponent },
  { path: "SearchCar", component: SearchCarComponent },
  { path: "CalcDayPrice", component: CalcDayPriceComponent },
  { path: "ListCars", component: ListCarsComponent },
  { path: "ListOrders", component: ListOrdersComponent },
  { path: "ListUsers", component: ListUsersComponent },
  { path: "Employee", component:EmployeeComponent},
  { path: "Login", component: LoginPageComponent },
  { path: "UserOrder", component: UserOrderComponent },
  { path: "UserAllOrders", component: UserAllOrdersComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    scrollPositionRestoration: 'enabled',
    anchorScrolling: 'enabled',
  })],
  exports: [RouterModule]
})
export class AppRoutingModule {
  
 }

