



export class Car{

private _carNumber : number;
public get carNumber() : number {
    return this._carNumber;
}
public set carNumber(v : number) {
    this._carNumber = v;
}



private _carType : string;
public get carType() : string {
    return this._carType;
}
public set carType(v : string) {
    this._carType = v;
}

private _mileage : number;
public get mileage() : number {
    return this._mileage;
}
public set mileage(v : number) {
    this._mileage = v;
}


private _picture : String;
public get picture() : String {
    return this._picture;
}
public set picture(v : String) {
    this._picture = v;
}


private _usable : string;
public get usable() : string {
    return this._usable;
}
public set usable(v : string) {
    this._usable = v;
}

private _available : string;
public get available() : string {
    return this._available;
}
public set available(v : string) {
    this._available = v;
}


private _branch : string;
public get branch() : string {
    return this._branch;
}
public set branch(v : string) {
    this._branch = v;
}

}