import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { UserService } from '../user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent implements OnInit {
  carList: any;
  rentCarList: any;
  typeCarList: any;
  today = new Date();

  constructor(private http: HttpClient, private userService: UserService, private router: Router) {
  }
  //***function calculate regular price*****/
  CalcPrice(dateD: Date, dateP: Date, price: number) {
    let dateDropOff = new Date(dateD).getTime();
    let datePicup = new Date(dateP).getTime();
    let days = Math.floor((dateDropOff - datePicup) / (1000 * 60 * 60 * 24));    
    return (days * price);    
  };
  //***function calculate overdue price*****/
  CalcOverduePrice(dateD: Date, price: number, dateP: Date) {
    let dateOverdue = new Date(this.today.getFullYear(), this.today.getMonth(), this.today.getDate()).getTime();
    let dateDropOff = new Date(dateD).getTime();
    let days = Math.ceil((dateOverdue - dateDropOff) / (1000 * 60 * 60 * 24));
    let datePicup = new Date(dateP).getTime();    
    if (dateDropOff >= dateOverdue) return 0;
    else
      return days * price;
  };
  //***function adding drop-off date to database and change car to available****
  async Payment(carId: number, id: number) {
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    if (confirm("Are you sure to confirm this payment?")) {
      document.getElementById('preloader').style.display = "block";
      await this.http.put("http:// www.serverapi.somee.com/api/RentCar/" + id, { ReturnCarIndeed: this.today }, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
      await this.http.put("http:// www.serverapi.somee.com/api/car/" + carId, { Available: 'yes' }, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
      this.ngOnInit();
      document.getElementById('preloader').style.display = "none";
    }
  };

  ngOnInit() {
     //*****if not employee role routing to home page****
    if (this.userService.userRole() != 'employee' || this.userService.userRole() == undefined) {
      this.router.navigate(['/HomePage']);
    }
    this.http.get("http:// www.serverapi.somee.com/api/car/").subscribe(t => this.carList = t);
    this.http.get("http:// www.serverapi.somee.com/api/RentCar/").subscribe(t => this.rentCarList = t);
    this.http.get("http:// www.serverapi.somee.com/api/typecar/").subscribe(t => this.typeCarList = t);
  }
}
