import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { UserService } from './../../user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-list-users',
  templateUrl: './list-users.component.html',
  styleUrls: ['./list-users.component.css']
})
export class ListUsersComponent implements OnInit {
  usersList: any;
  error: any;
  Years18 = Date.now() - 568025136000;
  constructor(private http: HttpClient, private userService: UserService, private router: Router) {
  }
  //****function to open form in template/html*****
  openForm() {
    document.getElementById("addUserForm").style.display = "block";
  }
  //****function to close form in template/html*****
  closeForm() {
    document.getElementById("addUserForm").style.display = "none";
  }
  //****Function adding new user to database*****/
  async AddNewUser(fName: string, lName: string, id: number, u_email: string, bDay: any, gender: string, userName: string, password: string, role: string) {
    document.getElementById('preloader').style.display = "block";
    this.closeForm();
    await this.http.post("http:// www.serverapi.somee.com/api/user/", {
      FirstName: fName,
      LastName: lName,
      UserID: id,
      UserName: userName,
      BirthDay: bDay,
      Gender: gender,
      Email: u_email,
      UserPassword: password,
      Role: role
    }).toPromise().catch(err => alert(err.error));
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }
  //****function to udate user in database****
  async Update(id: number, f_Nmae?: string, l_Name?: string, userId?: number, email?: string, birthDay?: any, gender?: string, userName?: string, password?: string, role?: string) {
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
    await this.http.put("http:// www.serverapi.somee.com/api/user/" + id, {
      FirstName: f_Nmae,
      LastName: l_Name,
      UserID: userId,
      Email: email,
      BirthDay: birthDay,
      Gender: gender,
      UserName: userName,
      UserPassword: password,
      Role: role
    }, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }
  //****function to delete user in database****
  async Delete(id: number) {
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
    await this.http.delete("http:// www.serverapi.somee.com/api/user/" + id, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }

  ngOnInit() {
    //*****if not admin role routing to home page****
    if (this.userService.userRole() != 'admin' || this.userService.userRole() == undefined) {
      this.router.navigate(['/HomePage']);
    }
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    this.http.get("http:// www.serverapi.somee.com/api/user/", { headers: authHeaders }).subscribe(h => this.usersList = h, error => { this.error = error; });
  }

}
