import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { UserService } from 'src/app/user.service';

@Component({
  selector: 'app-user-all-orders',
  templateUrl: './user-all-orders.component.html',
  styleUrls: ['./user-all-orders.component.css']
})
export class UserAllOrdersComponent implements OnInit {
  rentCarList: any;
  today = new Date();

  constructor(private http: HttpClient, private router: Router, private userService: UserService) { }

  ngOnInit() {
    //*****if not user role routing to home page****
    if (this.userService.userRole() != 'user' || this.userService.userRole() == undefined) {
      this.router.navigate(['/HomePage']);
    }
    this.http.get("http:// www.serverapi.somee.com/api/RentCar/").subscribe(t => this.rentCarList = t);
  }

}
