import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'availableCar'
})
//****returns of available cars****
export class AvailableCarPipe implements PipeTransform {

  transform(arrCars: any[]): any[] {
    if (arrCars != null) {
      let arrAvailableCars: Array<any> = [];
      for (let i = 0; i < arrCars.length; i++) {
        if (arrCars[i].Available == "no" || arrCars[i].Usable == "no") {
          continue;
        }

        else {
          arrAvailableCars.push(arrCars[i]);
        }
      }
      return arrAvailableCars;
    }
  }

}
