export class RentCar{

private _PickupCar : Date;
public get PickupCar() : Date {
    return this._PickupCar;
}
public set PickupCar(v : Date) {
    this._PickupCar = v;
}


private _ReturnCar : Date;
public get ReturnCar() : Date {
    return this._ReturnCar;
}
public set ReturnCar(v : Date) {
    this._ReturnCar = v;
}


private _CarID : number;
public get CarID() : number {
    return this._CarID;
}
public set CarID(v : number) {
    this._CarID = v;
}


private _UserID : number;
public get UserID() : number {
    return this._UserID;
}
public set UserID(v : number) {
    this._UserID = v;
}


}