import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';

import { UserService } from 'src/app/user.service';



@Component({
  selector: 'app-list-orders',
  templateUrl: './list-orders.component.html',
  styleUrls: ['./list-orders.component.css']
})
export class ListOrdersComponent implements OnInit {
  carList: any;
  rentCarList: any;
  usersList: any;
  today = new Date();

  constructor(private http: HttpClient, private userService: UserService, private router: Router) {
  }
  //****function to open form in template/html*****
  openForm() {
    document.getElementById("addOrderForm").style.display = "block";
  }
  //****function to close form in template/html*****
  closeForm() {
    document.getElementById("addOrderForm").style.display = "none";
  }
  //****Function adding new order to database*****/
  async AddNewOrder(carId: number, userId: number, picupDate: any, dropOffDate: any) {
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
    this.closeForm();
    await this.http.post("http:// www.serverapi.somee.com/api/RentCar/", {
      CarID: carId,
      UserID: userId,
      PickupCar: picupDate,
      ReturnCar: dropOffDate,
    },{ headers: authHeaders }).toPromise();
    await this.http.put("http:// www.serverapi.somee.com/api/Car/" + carId, {
      Available: 'no',
    }, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }
  //****Function checking drop-off date if undefined return date of today*****/
  OverdueDate(dropOffDate: any) {
    if (dropOffDate == undefined) {
      return this.today;
    }
    else return dropOffDate;
  }
  //****function to udate order in database****
  async Update(Id: number, carId?: number, picupDate?: any, dropOffDate?: any, dropOffOverdue?: any) {
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
    this.closeForm();
    await this.http.put("http:// www.serverapi.somee.com/api/RentCar/" + Id, {
      CarID: carId,
      PickupCar: picupDate,
      ReturnCar: dropOffDate,
      ReturnCarIndeed: dropOffOverdue
    }, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }
  //****function to delete order in database****
  async Delete(Id: number, carNumber: number) {
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
    this.closeForm();
    await this.http.put("http:// www.serverapi.somee.com/api/Car/" + carNumber, {
      Available: 'yes',
    }, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
    await this.http.delete("http:// www.serverapi.somee.com/api/RentCar/" + Id, { headers: authHeaders }).toPromise().catch(err => alert(err.error));
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }

  ngOnInit() {
    //*****if not admin role routing to home page****
    if (this.userService.userRole() != 'admin' || this.userService.userRole() == undefined) {
      this.router.navigate(['/HomePage']);
    }
    this.http.get("http:// www.serverapi.somee.com/api/car/").subscribe(t => this.carList = t);
    this.http.get("http:// www.serverapi.somee.com/api/RentCar/").subscribe(t => this.rentCarList = t);
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    this.http.get("http:// www.serverapi.somee.com/api/user/", { headers: authHeaders }).subscribe(h => this.usersList = h);
  }

}
