import { Component, OnInit } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Router } from '@angular/router';

import { Car } from '../../car';
import {TypeCar} from '../../typeCar';
import { UserService } from 'src/app/user.service';



@Component({
  selector: 'app-list-cars',
  templateUrl: './list-cars.component.html',
  styleUrls: ['./list-cars.component.css']
})
export class ListCarsComponent implements OnInit {
  carList:any;
  typeList:any;
  typeList2:any;
  car=new Car();  
  typeCar=new TypeCar();
  constructor(private http:HttpClient,  private userService: UserService, private router:Router) {
  
   }
  
//***converting image to base64 part 1***/
handleFileSelect(event){
  var files = event.target.files;
  var file = files[0];

if (files && file) {
    var reader = new FileReader();

    reader.onload =this._handleReaderLoaded.bind(this);

    reader.readAsBinaryString(file);
}
}
//***converting image to base64 part 2***/
_handleReaderLoaded(readerEvt) {
 var binaryString = readerEvt.target.result;
        this.car.picture= btoa(binaryString);      
}

 //****Function adding new car to database*****/
  async AddNewCar(){  
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
     this.closeForm();
    await this.http.post("http:// www.serverapi.somee.com/api/car/",{        
      CarID:this.car.carNumber,
      CarType:this.car.carType,
      Mileage:this.car.mileage,
      PictureBase64:this.car.picture,
      Usable:this.car.usable,
      Available:this.car.available,
      Branch:this.car.branch
    },{ headers: authHeaders }).toPromise().then(h=>console.log(h)).catch(err=>alert(err.error));
    this.ngOnInit();    
    document.getElementById('preloader').style.display = "none";
    
    
    //****Function adding new type of car to database*****/
  }
  async AddNewTypeCar(){
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    let year=((this.typeCar.YearManufacturer).toString()).slice(0,-6);
     this.typeCar.CarType=this.typeCar.ManufacturerNmae+" "+this.typeCar.Model+" "+year;
     document.getElementById('preloader').style.display = "block";
     this.closeForm();
     await this.http.post("http:// www.serverapi.somee.com/api/typecar/",{
      CarType:this.typeCar.CarType,
      ManufacturerNmae:this.typeCar.ManufacturerNmae,
      Model:this.typeCar.Model,
      DalyPrice:this.typeCar.DalyPrice,
      DelayDalyPrice:this.typeCar.DelayDalyPrice,
      YearManufacturer:this.typeCar.YearManufacturer,
      Transmission:this.typeCar.Transmission
    },{ headers: authHeaders }).toPromise().then(h=>console.log(h)).catch(err=>alert(err.error));
    this.car.picture=undefined;
    this.ngOnInit();    
    document.getElementById('preloader').style.display = "none";
    
  }
  //****function to open form in template/html*****
  openForm() {
    document.getElementById("addCarForm").style.display = "block";
  }  
  //****function to close form in template/html*****
  closeForm() {
    document.getElementById("addCarForm").style.display = "none";
  }
  //****function to udate car in database****
  async UpdateCar(id:number,cType?:string, mileage?:number,branch?:string,available?:string,usable?:string){
    let authHeaders=new HttpHeaders().set("Authorization",`Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);  
    document.getElementById('preloader').style.display = "block";    
    await this.http.put("http:// www.serverapi.somee.com/api/car/"+id,{
      CarType:cType,
      Mileage:mileage,
      Branch:branch,
      Available:available,
      Usable:usable,
      PictureBase64:this.car.picture
    },{headers:authHeaders}).toPromise().catch(err=>alert(err.error));
    this.car.picture=undefined;
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
     
  }
  //****function to udate type of car in database****
  async UpdateTypeCar(id:number,manufacturerNmae?:string,model?:string,dalyPrice?:number,delayDalyPrice?:number,yearManufacturer?:any,transmission?:string){
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
    await this.http.put("http:// www.serverapi.somee.com/api/typecar/"+id,{
      ManufacturerNmae:manufacturerNmae,
      Model:model,
      DalyPrice:dalyPrice,
      DelayDalyPrice:delayDalyPrice,
      YearManufacturer:yearManufacturer,
      Transmission:transmission
    },{headers:authHeaders}).toPromise().catch(err => alert(err.error));    
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }
//****function to detete car in database****
  async DeleteCar(carId:number){
    let authHeaders = new HttpHeaders().set("Authorization", `Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);
    document.getElementById('preloader').style.display = "block";
    await this.http.delete("http:// www.serverapi.somee.com/api/car/"+carId,{headers:authHeaders}).toPromise().catch(err => alert(err.error));    
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }
  //****function to delete type of car in database****
  async DeleteTypeCar(carTypeId:number,carId){
    let authHeaders=new HttpHeaders().set("Authorization",`Basic ${this.userService.loginUser.UserName}:${this.userService.loginUser.UserPassword}`);  
    document.getElementById('preloader').style.display = "block";    
    await this.http.put("http:// www.serverapi.somee.com/api/car/"+carId,{
      CarType:'none',Usable:'no'},{headers:authHeaders}).toPromise();
      await this.http.delete("http:// www.serverapi.somee.com/api/typecar/"+carTypeId,{headers:authHeaders}).toPromise().catch(err => alert(err.error));
    this.ngOnInit();
    document.getElementById('preloader').style.display = "none";
  }
  
  ngOnInit() {    
    //*****if not admin role routing to home page****
    if(this.userService.userRole()!='admin' || this.userService.userRole()==undefined){
      this.router.navigate(['/HomePage']);
    }
    this.http.get("http:// www.serverapi.somee.com/api/car/").subscribe(t=>{this.carList=t});
    this.http.get("http:// www.serverapi.somee.com/api/typecar/").subscribe(t=>{this.typeList=t,this.typeList2=t});
  }

}
