import { HttpClient, HttpHeaders } from '@angular/common/http';

export class UserService {

    constructor(private http: HttpClient) {
    }

    private _loginUser: any;
    public get loginUser(): any {
        return this._loginUser;
    }
    public set loginUser(v: any) {
        this._loginUser = v;
    }
    error: any;
    //****function to login user***
    async LogIn(userName: string, password: string) {
        let authHeaders = new HttpHeaders().set("Authorization", `Basic ${userName}:${password}`);
        await this.http.get("http:// www.serverapi.somee.com/api/user/" + userName, { headers: authHeaders })
            .toPromise().then(h => this.loginUser = h)
            .catch(x => Promise.reject(`error is ${x}`))
            .then(x => console.log('login succeeded'),
                err => console.log('wrong user name or password', err));;
        return new Promise(resolve => {
            if (this.loginUser != undefined) {
                return resolve(true);
            }
            else { return resolve(false); }
        });
    }
    //****finction returns status of login*****
    confirmLogin() {
        if (this.loginUser != undefined)
            return true;
        else return false;
    }
    //******function returns rolse of users***********
    userRole() {
        if (this.loginUser == undefined)
            return "user";
        else return this.loginUser.Role;
    }

    pickupDate: Date;
    index = 0;
    userCar: any[];
    choicesOfCar: any[] = [];
    //******function seving current and last 3 cars selected by user
    addCar(car: any, typeCar: any) {
        let arr = [];
        arr.push(car);
        arr.push(typeCar);
        this.userCar = arr;
        var dublicate = 'no';
        for (let i_available = 0; i_available < this.choicesOfCar.length; i_available++) {
            if (this.choicesOfCar[i_available].CarID == car.CarID) {
                dublicate = 'yes';
            }
        }
        if (dublicate == 'no') {
            if (this.choicesOfCar.length < 3) {
                this.choicesOfCar.push(car);
            }
            else {
                if (this.index < 2) {
                    this.choicesOfCar[this.index] = car;
                    this.index++;
                }
                else {
                    this.choicesOfCar[this.index] = car;
                    this.index = 0;
                }
            }
        }
    }
    //****function to clear user data */
    ClearUser() {
        this.loginUser = undefined;
        this.choicesOfCar = [];
        this.userCar = [];
        this.index = 0;
        this.pickupDate = undefined;
    }
}